import React, { Component } from 'react';
import Flexbox from 'flexbox-react';


class ContactPage extends Component {
  state = {
    email: "",
    message: "",

  }

  onChangeEmail(value) {
    this.setState({email: value})
  } 

  onChangeMessage(value) {
    this.setState({message: value})
  }


  render() {
    return (
        <Flexbox flex={1} alignItems="start" flexDirection='column'>
            <Flexbox flex={1} alignItems="center" style={{padding: 25}}>
                <p>KayN is a review system that combines decentralization and aspects of Reddit to make reviews more useful. Similar to Reddit posts, every review created can be both upvoted and downvoted. These votes, as well as how old the review is, calculates a score for the review. It is this score that determines how close to the top the review is.</p>
            </Flexbox>

            <Flexbox flex={1} alignItems="center" style={{padding: 25}}>
                <p>Along with reviews users themselves are scored as well, in the form of a level that adjusted based on review votes. So getting lots of upvotes will make you a higher level. It is a users level that determines how much impact their rating has on the overall rating. For instance a level 1 giving 5 stars would not do much but a level 100 giving 5 stars could make a big difference. This discerns between good and bad reviews, allowing any consumer to essentially make themselves a critic by getting so many upvotes.</p>
            </Flexbox>

            <Flexbox flex={1} alignItems="center" style={{padding: 25}}>
                <p>The main issue with how review systems currently work is trust. Consumers don’t fully trust the system so therefore don't fully trust the reviews, and businesses feel strong armed to pay else get their businesses hurt. With current review systems doing things like filtering out reviews they deem as not helpful or putting poor reviews at the top for businesses who don’t pay, it's not hard to see why there isn’t trust. Because KayN is controlled by a smart contract on Ethereum it decentralizes the whole system. This essentially means KayN the company is not the middle man between consumers and businesses, the smart contract is. The smart contract is also, totally public, totally transparent, and immutable. If you wanted you could write reviews or vote as transactions directly to the contract or even write your own client.</p>
            </Flexbox>

            <Flexbox flex={1} alignItems="center" style={{padding: 25}}>
                <p>Another large piece of KayN is leveraging the smart contract technology to give the small businesses a new way to interact with their consumers. By joining our premium plan they get access to a set of features that will allow them to use KayN as a tool for their business. One feature they will get access to is identity tokens, this will allow them to require a QR code or barcode on receipts to write a review. This guarantees reviews for their business are valid. The main feature they get access to is the ability to incentivize consumers to review by setting up competitions with rewards. A few examples:</p>
            </Flexbox>

            <Flexbox flex={1} alignItems="start" flexDirection="column" style={{paddingLeft: 100}}>
                <p> Every review today gets a coupon</p>
                <p> Every review with a score greater than 3 this week gets a free side</p>
                <p>The best 3 reviews this week win $100</p>
            </Flexbox>
            <Flexbox flex={1} alignItems="center" style={{padding: 25}}>
                <p>To rank how ‘good’ a review is will always be based on users' votes. This means businesses will always be unable to incentivize people to write positive reviews, just reviews that consumers determined useful. This feature is one of the most important as it will incentivize consumers to write a review everytime they make a purchase for rewards and to level up.</p>
            </Flexbox>
            
        </Flexbox>
    );
  }
}

export default ContactPage;



