import React, { Component } from 'react';
import Flexbox from 'flexbox-react';

import tab3 from "./images/tab3.svg";

class ContactPage extends Component {
  state = {
    email: "",
    message: "",

  }

  onChangeEmail(value) {
    this.setState({email: value})
  } 

  onChangeMessage(value) {
    this.setState({message: value})
  }


  render() {
    return (
        <Flexbox flex={1} alignItems="center">
            <div style={{height: 100}}/>
            <Flexbox flex={1} flexDirection='row' style={{backgroundImage: "url(" + tab3 + ")", top:'0', bottom:'0', left:'0', right:'0', position: 'absolute', backgroundSize: 'stretch'}}>

              <Flexbox flex={.4} flexDirection="column" alignItems="center" style={{padding: 15}}> 
                <Flexbox flex={.5}/>
                <Flexbox flex={.5} style={{padding: 0}}>
                  <p style={{fontSize: 45, textAlign: 'start'}}>Contact Us!</p>
                </Flexbox>
                <Flexbox flex={.5}>
                  <p style={{fontSize: 30, textAlign: 'start'}}>Please reach out with any questions, inquires, or ideas!</p>
                </Flexbox>
                <Flexbox flex={.5}/>
              </Flexbox>
              <Flexbox flex={.1} />

              <Flexbox flex={.5} flexDirection="column" style={{paddingLeft: 150, paddingTop: 100}}>
                <Flexbox flex={.3}/>
                <Flexbox flex={.7}>
                  <form>
                    <Flexbox flexDirection='column'>

                      <Flexbox flexDirection="column" style={{paddingBottom: 25}}>
                        <label>
                          Email:
                        </label>
                        <input style={{width: 350}} type="text" value={this.state.email} onChange={() => this.onChangeEmail()}/>
                        
                      </Flexbox>

                      <Flexbox flexDirection="column" style={{paddingBottom: 25}}>
                        <label>
                          Message:
                        </label>
                        <input style={{width: 350, height: 200}} type="text" value={this.state.message} onChange={() => this.onChangeMessage()}/>
                      </Flexbox>

                      <Flexbox style={{paddingTop: 15,}}>
                        <input style={{width: 150}} type="submit" value="Submit" />
                      </Flexbox>

                    </Flexbox>
                  </form>
                </Flexbox>
            
              </Flexbox>

            </Flexbox>
        </Flexbox>
    );
  }
}

export default ContactPage;



